@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  font-family: "Montserrat", sans-serif;
}

.wrapper {
  border-radius: 20px;
  width: 100%;
  text-align: center;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
}

header h2 {
  font-weight: 600;
}

textarea:focus,
input:focus {
  outline: none;
}

.inputField {
  border: none;
  background: none;
  font-size: 20px;
  font-weight: 600;
}

.subButton {
  width: 50px;
  height: 50px;
  border: none;
  background: #f08a5d;
  border-radius: 50%;
  transition: background 200ms ease-out;
}
.subButton:hover {
  background: #b83b5e;
}

.subButton p {
  font-size: 30px;
  padding: auto;
  margin: 0px;
  color: white;
}

.inputWrapper {
  width: 50%;
  border: 1px solid #b83b5e;
  border-radius: 15px;
}

.inputTextArea {
  width: 100%;
  border: 1px solid #b3afaf;
  border-radius: 10px;
  font-size: 15px;
}

.noteWrapper {
  text-align: left;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  border-radius: 10px;
}

.myButton {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  transition: background 200ms ease-out;
  color: white;
}

.del {
  background: #b83b5e;
}
.del:hover {
  background: red;
}
.edit {
  background: #f08a5d;
}
.edit:hover {
  background: #c26c04;
}

textarea:focus,
input:focus {
  outline: none;
}

.updateInput {
  border: 1px solid #b3afaf;
  border-radius: 5px;
}

